import { setGlobalData, getGlobalData } from "@/common/constants";
import { ALL_UNREAD_MESSAGE_SUM } from "@/common/globalMsgKeys";
import { getAndHandOfficialNewsList, getAndHandleUserMessageList, getAndHandleInteractiveMessageList } from "@/common/requestFunctions";
/**
 * @description 初始化未读消息
 * @param {number} userId 用户的id
 *
 */
export async function initUnreadMessageSum(userId) {
    let unreadSum = 0;
    let messageList = await getAndHandleUserMessageList(userId);
    let officialNewsList = await getAndHandOfficialNewsList(userId);
    messageList.forEach((item) => {
        if (item.unreadSum > 0) {
            unreadSum = unreadSum + item.unreadSum;
        }
    });
    officialNewsList.forEach((item) => {
        if (item.unreadSum > 0) {
            unreadSum = unreadSum + item.unreadSum;
        }
    });
    let interactiveList_unreadSum = await getAndHandleInteractiveMessageList(userId);
    console.log(interactiveList_unreadSum);
    unreadSum = unreadSum + interactiveList_unreadSum;
    console.log("unreadSum", unreadSum);
    setGlobalData(ALL_UNREAD_MESSAGE_SUM, unreadSum);
    if (unreadSum > 0) {
        uni.setTabBarBadge({
            index: 2,
            text: String(unreadSum)
        });
    }
    else {
        uni.removeTabBarBadge({
            index: 2
        });
    }
}
/**
 * @function 设置未读消息的数量并在底部的按钮上显示
 * @param changeNumber 变化的数量正数为加负数为减 不填时为更新
 */
export function changeUnreadMessageSum(changeNumber = 0) {
    let unreadSum = 0;
    try {
        unreadSum = getGlobalData(ALL_UNREAD_MESSAGE_SUM);
        unreadSum = unreadSum + changeNumber;
    }
    catch {
        if (changeNumber > 0) {
            unreadSum = unreadSum + changeNumber;
        }
    }
    setGlobalData(ALL_UNREAD_MESSAGE_SUM, unreadSum);
    if (unreadSum > 0) {
        uni.setTabBarBadge({
            index: 2,
            text: String(unreadSum)
        });
    }
    else {
        uni.removeTabBarBadge({
            index: 2
        });
    }
}
