import md5 from 'js-md5';
import { Protocol, REQUEST_SUCCEEDED_CODE, ServerDomain, random16String, timestamp, key } from '../common/constants';
let showModel = true;
/**
 * @function 获取天气
 * @returns {Promise<any>}
 */
export async function getWeatherInfo() {
    return new Promise((resolve, reject) => {
        uni.request({
            method: 'GET',
            url: 'https://www.mulantools.top/Api/weather_api',
            header: {
                'content-type': 'application/x-www-form-urlencoded',
            },
            success: function (result) {
                // resolve调用后，即可传递到调用方使用then或者async+await同步方式进行处理逻辑
                resolve(result);
                return result;
            },
            fail: function (e) {
                console.log('error in...');
                // reject调用后，即可传递到调用方使用catch或者async+await同步方式进行处理逻辑
                reject(e);
            },
        });
    });
}
/**
 * @function 封装后的请求
 * @param req 请求参数
 * @returns {Promise<RequestSuccessCallbackResult>}
 */
export async function request(req) {
    let token = '';
    try {
        token = uni.getStorageSync('token');
    }
    catch {
    }
    if (!token) {
        console.log('未登錄');
    }
    let random16 = random16String();
    let time = timestamp;
    let header = {
        ...req.data.header,
        't': time,
        'r': random16,
        's': md5(time + random16 + key),
        'token': token
    };
    return new Promise((resolve, reject) => {
        uni.request({
            method: req.data.method,
            url: `${Protocol}://${ServerDomain}/${req.data.group}/${req.data.action}`,
            data: req.data.data,
            header: header,
            success: function (result) {
                // resolve调用后，即可传递到调用方使用then或者async+await同步方式进行处理逻辑
                resolve(result);
                let data = result.data;
                let resCode = data.code;
                let userMsg = data.userMsg;
                if (resCode != REQUEST_SUCCEEDED_CODE) {
                    if (resCode === "A0001" || resCode === "A0005") {
                        if (showModel) {
                            showModel = false;
                            uni.showModal({
                                title: '登录过期',
                                content: '登录过期请重新登录',
                                success: function (res) {
                                    if (res.confirm) {
                                        uni.setStorage({
                                            key: 'token',
                                            data: null
                                        });
                                        uni.navigateTo({
                                            url: '/pages/login/login'
                                        });
                                        showModel = true;
                                    }
                                    else {
                                        showModel = true;
                                    }
                                }
                            });
                        }
                    }
                    else {
                        uni.showToast({
                            title: userMsg,
                            icon: 'none'
                        });
                    }
                }
                return result;
            },
            fail: function (e) {
                console.log('error in...');
                // reject调用后，即可传递到调用方使用catch或者async+await同步方式进行处理逻辑
                reject(e);
            },
        });
    });
}
